import React from 'react';
import './activeCases.scss';
import If from '../../library/If';
import { Link } from 'react-router-dom';

class ActiveCases extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      activeCases: [],
      oneActiveCase: []
    };
  }

  componentWillMount = () => {
    let activeCases = this.props.cases.filter(file => {
      return file.isActive === true;
    })

    this.setState({ activeCases: activeCases });
  }
  
  displayOneActiveCase = (idx) => {
    let oneActiveCase = this.state.activeCases[idx];

    this.props.displayOneActiveCase(idx);

    this.setState({ 
      oneActiveCase: oneActiveCase,
     });
  }

  render(){

    return(
      <div id="active-cases">
        <If condition={this.props.displayDashboard}>
          <div id="active-cases">
            <h4>Active Cases</h4>
            <div className="flex-container">
              <h5>Name</h5>
              <h5>Case Number</h5>
              <h5>Court</h5>
              <h5>Deposition</h5>
              <h5> </h5>
            </div>

            <ul>
              {this.state.activeCases.map((activeCase, idx) => (
                <Link to={`/cases/${activeCase.id}`}>
                  <li className="display-table" key={idx}>
                    <div className="case-name">{activeCase.name}</div>
                    <div className="case-number">{activeCase.caseNumber}</div>
                    <div className="case-court">{activeCase.court}</div>
                    <div className="case-depositions">{activeCase.depositions.length}</div>
                    <div className="more-dots">...</div>
                  </li>
                </Link>
              ))}
            </ul>
          </div>  
        </If>
      </div>
  )}
}

export default ActiveCases;